declare var $:any 
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ToastController,Events } from 'ionic-angular';
import { HttpServicesProvider } from '../../../../providers/http-services';
import { Utils } from '../../../../providers/Utils';
import { WorkflowServiceProvider } from '../../../../providers/workflow-service';
import { StorageProvider } from '../../../../providers/storage';
import { PhoneDeviceProvider } from '../../../../providers/phone-device';
import { ToolsProvider } from '../../../../providers/tools';
import { THROW_IF_NOT_FOUND } from '@angular/core/src/di/injector';
/**
 * Generated class for the LmtagreportweekinfoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lmtagreportweekinfo',
  templateUrl: 'lmtagreportweekinfo.html',
})
export class LmtagreportweekinfoPage {

  public formdata=[
    {'name':'周报描述','index':'description','type':'textarea',"placeholder":'请填写周报描述','starttoggle':true,'toggleindex':'createtime','plantitle':'基本信息'},
    {'name':'周报日期','index':'code','type':'text',"placeholder":'请选择日期','disabled':true},
    {'name':'检修开始','index':'phase','type':'text','placeholder':'请填写'},
    {'name':'编制人','index':'creatername','type':'text','placeholder':'请填写编制人'},
    {'name':'编制时间','index':'createtime','type':'text',"placeholder":'请填写编制时间'}
  ];
  public forminfo={};
  public id='';
  public type='';
  public wfstatusname='';
  public table1length = 0;
  public tabledata= [];
  public table2length = 0;
  public table2data= [];
  public table3length = 0;
  public table3data= [];
  // public btnhide = false;
  public pageControl ={}
  public projectid='';
  constructor(public events: Events,public storage: StorageProvider,public navCtrl: NavController,public toastCtrl: ToastController, public navParams: NavParams,public httpService:HttpServicesProvider,public workflow: WorkflowServiceProvider,
    public tools:ToolsProvider,
    public phoneDevice: PhoneDeviceProvider,) {
      this.phoneDevice.phoneBacktoPop(this.navCtrl);
      this.projectid = this.storage.get(StorageProvider.LM_PROJECTID);
      this.id =this.navParams.get('id');
      this.type =this.navParams.get('type');
      this.initinfo()
      this.initlist();
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad PiinoutinfoPage');
  }
  ionViewWillEnter() {
    this.events.subscribe('workflowPage', (data, time) => {
       if(data){
         this.initinfo();
         this.events.unsubscribe('workflowPage');
       }
     })
   }
   initPrjname(id){
    this.httpService.get('/lmtagProject/detail/'+id, null, (result) => {
      if(result.code == 0){
        this.forminfo['prjname'] =result.data.projectname;
      }
    })
    // 
   }
   collapseClick(index){//区块收缩/展开
    $('page-lmtagreportweekinfo #collapse'+index).toggle();
    $('page-lmtagreportweekinfo .collapse_show_icon'+index).toggle();
    $('page-lmtagreportweekinfo .collapse_hide_icon'+index).toggle();
  }
  pageElementControl(data){
    this.pageControl = data;
  }
  async pageElement(wfstatus,wfid){// 初始化页面资源权限
    // var that = this;
    const data = await this.httpService.getPageControlElementAnsy("lmtagReportWeekinfo_form",wfstatus,wfid);
    this.pageElementControl(data);
    var array = this.httpService.pageDate(data);
    this.formdata = this.httpService.getpagedata(this.formdata,array);
    
   
  }
  initstatus(status){
    this.httpService.getDictName("LMTAG_NON_CONFORMITY_WFSTATUS",status, res => {
      // console.log(res)
      if (res !== "") {
       this.forminfo['wfstatusname'] =res;
      }
    })
  }
  initConformitycode(conformityid){
    this.httpService.get('/lmtagReportInfo/detail/'+conformityid, null, (result) => {
      if(result.code == 0){
        var wfstatus = result.data.wfstatus;
				var code = result.data.code;
        this.httpService.getDict("LMTAG_NON_CONFORMITY_WFSTATUS", res => {
          if(res!=null&&res.length>0){
						for(var i=0;i<res.length;i++){
							if(res[i].value==wfstatus){
                $('#_conformitycode').html('<a id="conformitycodename" style="width: 100%;text-align: right;display: block;">'+code+'('+res[i].name+')'+'</a>')
								// $('#_conformitycode').val(code + '('+res[i].name+')');
                $('#conformitycodename').click(function(){
                  console.log(23456)
                })
							}
						}
					}
         
        })
      }
      
        
      // if(result.code == 0){
      //   this.forminfo['prjname'] =result.data.projectname;
      // }
    })
    
  }
  selectitem(item){
    // if(item.index == 'acceptlevel'){
    //   if(this.forminfo['acceptlevel'] == "LEVEL2"){

    //   }
    // }
    // console.log(item)
  }
  initinfo(){
    var that = this;
    if(this.type == 'edit'){
      var url = "/query/danger/lmtagReportWeek/findOne?id=" + this.id;
      this.httpService.get(url, null, (result) => {
        console.log(result)
        if (result.code == 0) {
          if (result.data.length > 0) {
            this.forminfo = result.data[0];
            this.pageElement(result.data[0].wfstatus,result.data[0].wfid);
            this.initstatus(result.data[0].wfstatus);
            this.initPrjname(result.data[0].prjid);
            this.initConformitycode(result.data[0].conformityid);
            if(result.data[0].checkresult == "HG"){
              this.forminfo['checkresult'] =  1;
            }
            if(result.data[0].checkresult == "BHG"){
              this.forminfo['checkresult'] =  0;
            }
            // this.httpService.initDeptName(result.data[0].resgroup,'resgroupname',this.forminfo )
            this.httpService.initUserName(result.data[0].woperson,'wopersonname',this.forminfo )
            this.httpService.initUserName(result.data[0].creater,'creatername',this.forminfo )
            this.httpService.initUserName(result.data[0].trialrunperson,'trialrunpersonname',this.forminfo )
            this.httpService.initUserName(result.data[0].noticeperson,'noticepersonname',this.forminfo )
            this.httpService.initUserName(result.data[0].assetdeptperson,'assetdeptpersonname',this.forminfo )
            this.httpService.initUserName(result.data[0].chief,'chiefname',this.forminfo )
            this.httpService.initUserName(result.data[0].workperson,'workpersonname',this.forminfo )
            this.httpService.initUserName(result.data[0].checkperson,'checkpersonname',this.forminfo )
            // this.httpService.inittime('requirefinishtime',this.forminfo);
            // this.httpService.inittime('findtime',this.forminfo);
            // // this.httpService.inittime('createtime',this.forminfo);
            // this.httpService.inittime('noticetime',this.forminfo);
            // this.httpService.inittime('noticetime1',this.forminfo);
            // this.httpService.inittime('checktime',this.forminfo);
            // this.httpService.inittime('checktime1',this.forminfo);
            // this.httpService.inittime('checktime2',this.forminfo);
            this.workflow.workflowcheckAuth(this.forminfo,'page-lmtagNonConformityinfo');//检查流程  
          }
        }
      });
    }else{
      this.id = Utils.getUuid();//id
      this.forminfo={
        dailyid: this.id,
        projectid:this.projectid,
        prjname:'',
        code:'',
        description:'',
        abnormallevel:'',
        requirefinishtime:'',
        woperson:'',
        wounitname:'',
        wounit:'',
        findtime:'',
        creater:'',
        createtime:'',
        wfstatus:'NEW'
      }
      this.initstatus('NEW');
      this.pageElement('NEW',null);
    }
      
  }
  initlist(){
    this.httpService.get('/lmtagReportInfo/list?reportid='+this.id+'&sort=createtime&order=desc&_search=false&rows=-1&page=1&sidx=&sord=asc', null, (result) => {
      this.tabledata = result.rows;
      this.table1length =result.records;
    })
    this.httpService.get('/lmtagReportProject/list?reportid='+this.id+'&rptype=NEXT&sort=createtime&order=desc&_search=false&rows=-1&page=1&sidx=&sord=asc', null, (result) => {
      this.table2data = result.rows;
      this.table2length =result.records;
    })
    this.httpService.get('/lmtagReportContact/list?reportid='+this.id+'&rptype=NEXT&sort=createtime&order=desc&_search=false&rows=-1&page=1&sidx=&sord=asc', null, (result) => {
      this.table3data = result.rows;
      this.table3length =result.records;
    })
   }
  submit(){
    if(this.tools.initform(this.formdata,this.forminfo)){
             this.httpService.initsavetime('requirefinishtime',this.forminfo);
            this.httpService.initsavetime('findtime',this.forminfo);
            this.httpService.initsavetime('createtime',this.forminfo);
            this.httpService.initsavetime('noticetime',this.forminfo);
            this.httpService.initsavetime('noticetime1',this.forminfo);
            this.httpService.initsavetime('checktime',this.forminfo);
            this.httpService.initsavetime('checktime1',this.forminfo);
            this.httpService.initsavetime('checktime2',this.forminfo);
           this.httpService.postform('/lmtagReportWeek/save', 'lmtagReportWeek', this.forminfo, (res) => {
          if(res.code==0){
            this.initinfo();
            this.toastCtrl.create({
              position:'bottom',
              duration:1500,
              message:'保存成功',
            }).present();
          }else{
            this.toastCtrl.create({
              position:'bottom',
              duration:1500,
              message:'保存失败',
            }).present();
          }
        })
     return true;
  }else{
    return false;
  }
  
   
  }
  historyview(){//历史
    this.navCtrl.push('HistorylistPage',{
      wfid:this.forminfo['wfid'],
      title:'流程历史',
    });
  }
  workflowStart() { //发送流程
    // this.saveStr = "true";
      if(this.submit() !=false){
        if (this.forminfo['wfid'] != "" && this.forminfo['wfid'] != null) {
          this.navCtrl.push('WorkflowroutePage', { 'busstype': 'lmtagReportDaily', 'bussid': this.id, 'wfid': this.forminfo['wfid'] });
        } else {
          //发送流程
          this.navCtrl.push('WorkflowstartPage', { 'busstype': 'lmtagReportDaily', 'bussid': this.id });
        }
      }
  }
}



